  font-family: 'Open Sans';
  src: url(/fonts/fonts/'OpenSans-Semibold.woff2') format('woff2'),
  url(/fonts/fonts/'OpenSans-Semibold.woff') format('woff'),
  url(/fonts/fonts/'OpenSans-Semibold.ttf') format('truetype');
